import React from 'react'
import Link from 'gatsby-link'
import { graphql } from 'gatsby'
import Layout from '../components/layouts'

export default ({ data }) => {
  return (
  <Layout>
    <h4>{data.allMarkdownRemark.totalCount} Posts</h4>
  	<div className="animated fadeInRight">
      {data.allMarkdownRemark.edges.map(({ node }) => (
        <div key={node.id} className="p-3 border-bottom border-light">
          <Link to={node.fields.slug}>
          <p className="h2">
            {node.frontmatter.title}{" "}
            <span color="#BBB">â€” {node.frontmatter.date}</span>
          </p>
          </Link>
          <p>{node.excerpt}</p>
        </div>
      ))}
  	</div>
    <Link to="/"><i className="fa fa-angle-double-left" aria-hidden="true"></i><i className="fa fa-angle-double-left" aria-hidden="true"></i> Back to home</Link>
  </Layout>
 );
};

export const blogquery = graphql`
  query BlogQuery {
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          fields {
            slug
          }
          excerpt
        }
      }
    }
  }
`;