import React from 'react'
import PropTypes from 'prop-types'
import { StaticQuery, graphql } from "gatsby"

import { ApolloProvider } from 'react-apollo';
import { ApolloClient } from 'apollo-client';
import { ApolloLink } from 'apollo-link';
import { HttpLink } from 'apollo-link-http';
import { onError } from 'apollo-link-error';
import { InMemoryCache } from 'apollo-cache-inmemory';

import 'font-awesome/css/font-awesome.min.css'
import 'bootstrap/dist/css/bootstrap.min.css' 
import 'mdbreact/dist/css/mdb.css'
import './style.css'
import Header from '../Header'
import Sidebar from '../Sidebar'
import Navtop from '../Navtop'
import Footer from '../Footer'
import ScrollButton from '../ScrollButton'

import fetch from 'node-fetch';
// import { createHttpLink } from 'apollo-link-http';
// const link = createHttpLink({ uri: '/graphql', fetch: fetch });

const GITHUB_BASE_URL = 'https://api.github.com/graphql';

const httpLink = new HttpLink({
  uri: GITHUB_BASE_URL, 
  fetch: fetch,
  headers: {
    authorization: `Bearer ${
      process.env.GATSBY_APP_GITHUB_PERSONAL_ACCESS_TOKEN
    }`,
  },
});

const errorLink = onError(({ graphQLErrors, networkError }) => {
  if (graphQLErrors) {
    // do something with graphql error
  }

  if (networkError) {
    // do something with network error
  }
});

const link = ApolloLink.from([errorLink, httpLink]);

const cache = new InMemoryCache();

const client = new ApolloClient({
  link,
  cache,
});

const Layout = ({ children, data }) => (
  <ApolloProvider client={client}>
     <StaticQuery
        query={graphql`
          query LayoutQuery {
           site {
             siteMetadata {
              title
              avatar
              author
              bio
             }
           }
            allMarkdownRemark {
              totalCount
            }
            allFile(filter: {relativeDirectory: {eq: "static/partners"}}) {
              edges {
                node {
                  id
                  name
                  publicURL
                }
              }
            }
         }
       `}
       render={data => (
         <>
        <div className="container">
              <div className="row">
                <div className="col col-sm-3 border-right border-light">
                  <div className="row">
                    <div className="col-12 mb-1">
                      <Header siteTitle={data.site.siteMetadata} />
                    </div>
                    <div className="col-12">
                      
                      <div className="row d-flex h-100">
                        <div className="col-12 py-3">
                          <Sidebar
                           postCount={data.allMarkdownRemark.totalCount}
                           partners={data.allFile}       
                           />
                      
                        </div>
                      </div>

                    </div>
                  </div>
                </div>
                <div className="col col-sm-9 px-5">
                    <Navtop siteTitle={data.site.siteMetadata.title} />
                  <div className="row">
                    {children}
                  </div>
                </div>
                    <Footer />   
                    <ScrollButton scrollStepInPx="50" delayInMs="16.66"/> 
              </div>
        </div>
      </>
     )}
   />
  </ApolloProvider>
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout

